.navbar {
	/* background-color: white; */
	background-color: #f8f7f8;
	/*opacity: 0.7;*/
	min-height: 38px;
	font-family: "Roboto Condensed";
	text-transform: uppercase;
	padding-left: 5vw;
	letter-spacing: 2px;
	/* border-bottom: 1px solid lightgray; */
	box-shadow: 0 1px 2px lightgray;
}

.sticky {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 10;
}

.nav-links {
	padding: 12px 10px;
	font-size: 12px;
	color: black;
	/*font-weight: bold;*/
	text-align: center;
	text-decoration: none;
	display: block;
}

.nav-links:hover {
	color: gray;
	text-decoration: none;
	/*background-color: white;*/
}

.nav-expand {
	float: left;
}

.nav-collapse .nav-collapse-links {
	display: none;
}

.nav-collapse i {
	visibility: hidden;
	padding: 6px 15px;
	color: black;
	float: right;
	transition-duration: .3s;
}

.nav-collapse i:hover {
	color: gray;
}

@media (max-width: 800px) {
	.navbar {
		padding-left: 0;
	}

	.nav-expand {
		display: none;
	}

	.nav-links {
		border-bottom: 1px solid lightgray;
	}

	.nav-collapse .nav-collapse-links.collapse{
		display: block;
	}

	.nav-collapse i {
		visibility: visible;
		position: absolute;
		right: 0;
		top: 0;
	}
}
